import React, { useEffect, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { useData } from "../../context/DataContext";
import "./details.css";
import Header from "../../components/Header";
import { useParams } from "react-router-dom";
import { ProjectInterface } from "../../types/types";
import { StateProps } from "../../types/context";

const Details = () => {
  const {
    projects,
    nameToUrl,
  }: { projects?: ProjectInterface[]; nameToUrl?: Function } = useData();
  const [project, setProject] = useState<ProjectInterface | StateProps>({});
  const { id }: { id: string } = useParams();
  useEffect(() => {
    if (projects && nameToUrl) {
      const project = projects.find((el) => nameToUrl(el.title) === id);
      setProject(project || {});
    }
  }, [projects, id, nameToUrl]);
  const showSlides = (): JSX.Element =>
    (project.images || [])?.map((el: string, i: number) => (
      <SwiperSlide key={i}>
        <div className="upper_heading swiper_top_bar">
          {" "}
          <i></i> &nbsp;
        </div>{" "}
        <div className="img" style={{ backgroundImage: `url(${el})` }}></div>
      </SwiperSlide>
    ));

  return (
    <div className="detail_project">
      <Header admin={true} />
      <div className="about_container">
        <div className="container">
          <div className="project_content">
            <h1 className="title_project"> {project.title} </h1>
            <div className="slider_wrapper_project">
              <Swiper
                pagination={{ clickable: true }}
                initialSlide={1}
                loop={true}
                effect="flip"
              >
                {showSlides()}
              </Swiper>
            </div>
            <div className="content">
              <h1>About This Project</h1>
              <p dangerouslySetInnerHTML={{ __html: project.details }} />
              <h1>Resources</h1>
              <p>
                The project is online at:{" "}
                <span className="fancy">
                  <a href={project.link}>{project.link}</a>
                </span>{" "}
              </p>
            </div>
            <footer className="project_footer">
              <h4>Let's talk</h4>
              <p>
                Wanna get in touch or talk about a project?
                <br /> Feel free to contact me via email at{" "}
                <span className="fancy">
                  <a href="mailto:adeebahmad0010@gmail.com">
                    adeebahmad0010@gmail.com
                  </a>
                </span>
              </p>
            </footer>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Details;
